import StorageKey from '@/constants/StorageKey';
import {
  FilterExamIdProps,
  FilterSourceTypeProps,
} from '@/pages/Library/utils/filterArr';
import storage from '@/utils/storage';

export interface TrueAnswer {
  answer: string;
  point: number;
  index: string;
}

export interface QuickItem {
  _id: string;
  checkAnswer: TrueAnswer;
  options: string[];
  parse: string;
  question: string;
  subject: string;
  sourceType: string;
  questionId: string;
}

export interface ExamQuestionItem {
  _id: string;
  subject: string;
  sourceType: string;
  question: string;
  questionId: string;
  subQuestionId?: string;
  parse: string;
  date: string;
  tags: string[];
  checkAnswers: TrueAnswer[][];
  optionCount?: number[];
  isOption: boolean;
}

export interface SelectAnswerItem {
  [key: number]: { [key: string]: string };
}
export interface ActionProps {
  type: string;
  tabType?: string;
  quickTrueAnswer?: TrueAnswer[];
  selectAnswer?: { [key: number]: string };
  quick?: QuickItem[];
  topic?: ExamQuestionItem[];
  exam?: {
    exam: FilterSourceTypeProps;
    examHis: FilterExamIdProps;
  };
  examQuestion?: ExamQuestionItem[];
  examSelectAnswer?: SelectAnswerItem;
  topicSelectAnswer?: SelectAnswerItem;
  ejuSubjectValue?: string[];
  userInfo?: {
    avatar: string;
    nickName: string;
    gender: number;
  };
}

export type ReducerProps = React.Reducer<ActionProps, ActionProps>;

const reducer = <T extends ActionProps>(state: T, action: T): T => {
  const {
    type,
    tabType,
    quickTrueAnswer,
    selectAnswer,
    quick,
    exam,
    examQuestion,
    examSelectAnswer,
    topicSelectAnswer,
    topic,
    ejuSubjectValue,
    userInfo,
  } = action;
  // 缓存state状态
  storage.setSession(
    StorageKey.PROJECTSTATE,
    JSON.stringify({ ...state, ...action })
  );
  switch (type) {
    case 'setTabType':
      // 题库首页类型
      return {
        ...state,
        tabType,
      };
    case 'setQuickTrueAnswer':
      // 智能速答正确答案
      return {
        ...state,
        quickTrueAnswer,
      };
    case 'setSelectAnswer':
      // 只能速答选择的答案
      return {
        ...state,
        selectAnswer,
      };
    case 'setQuick':
      // 设置题目列表
      return {
        ...state,
        quick,
      };
    case 'setExam':
      // 设置考试信息
      return {
        ...state,
        exam,
      };
    case 'setTopic':
      // 设置专题模拟
      return {
        ...state,
        topic,
      };
    case 'setExamQuestion':
      // 设置考试具体题目
      return {
        ...state,
        examQuestion,
      };
    case 'setExamSelectAnswer':
      // 设置考试选择答案
      return {
        ...state,
        examSelectAnswer,
      };
    case 'setTopicSelectAnswer':
      // 设置专题模拟选择答案
      return {
        ...state,
        topicSelectAnswer,
      };
    case 'setEjuSubjectValue':
      return {
        ...state,
        ejuSubjectValue,
      };
    case 'setUserInfo':
      return {
        ...state,
        userInfo,
      };
    default:
      return state;
  }
};

export default reducer;
