const storage = {
  /** localStorage Start */
  setLocal(k: string, v: string): boolean {
    return storage.setItem(window.localStorage, k, v);
  },

  getLocal(k: string): string {
    return storage.getItem(window.localStorage, k);
  },

  removeLocal(k: string): boolean {
    return storage.removeItem(window.localStorage, k);
  },

  clearLocal(): boolean {
    return storage.clear(window.localStorage);
  },
  /** localStorage End */

  /** sessionStorage Start */
  setSession(k: string, v: string): boolean {
    return storage.setItem(window.sessionStorage, k, v);
  },

  getSession(k: string): string {
    return storage.getItem(window.sessionStorage, k);
  },

  removeSession(k: string): boolean {
    return storage.removeItem(window.sessionStorage, k);
  },

  clearSession(): boolean {
    return storage.clear(window.sessionStorage);
  },
  /** sessionStorage End */

  setItem(s: Storage, k: string, v: string): boolean {
    if (!storage.test(s)) return false;

    s.setItem(k, v);

    return true;
  },

  getItem(s: Storage, k: string): string {
    if (!storage.test(s)) return '';

    return s.getItem(k) || '';
  },

  removeItem(s: Storage, k: string): boolean {
    if (!storage.test(s)) return false;

    s.removeItem(k);

    return true;
  },

  clear(s: Storage): boolean {
    if (!storage.test(s)) return false;

    s.clear();

    return true;
  },

  test(sto: Storage): boolean {
    if (sto) {
      return true;
    }
    return false;
  },
};

export default storage;
