import React, { Suspense, lazy, useReducer } from 'react';
import { Switch, Redirect } from 'react-router-dom';
import ErrorBoundary from '@/components/ErrorBoundary';
import LoadingPage from '@/components/LoadingPage';
import RouterProxy from '@/components/RouterProxy';
import { ProviderContext, defaultStore } from '@/reducer/context';
import reducer from '@/reducer/reducer';
import storage from '@/utils/storage';
import StorageKey from '@/constants/StorageKey';

const Home = lazy(() => import('../pages/Home'));
const Login = lazy(() => import('../pages/Login'));
const Register = lazy(() => import('../pages/Login/Register'));
const Complete = lazy(() => import('../pages/Login/Complete'));
const CompleteAll = lazy(() => import('../pages/Login/CompleteAll'));
const CompleteShow = lazy(() => import('../pages/Login/CompleteShow'));
const Msg = lazy(() => import('../pages/Msg'));
const MsgDetail = lazy(() => import('../pages/Msg/Detail'));
const Setting = lazy(() => import('../pages/Setting'));
const Safe = lazy(() => import('../pages/Setting/Safe'));
const ChangePwd = lazy(() => import('../pages/Setting/ChangePwd'));
const Feedback = lazy(() => import('../pages/Setting/Feedback'));
const Quick = lazy(() => import('../pages/Library/Quick'));
const QuickResult = lazy(() => import('../pages/Library/Quick/Result'));
const Parsing = lazy(() => import('../pages/Library/Quick/Parsing'));
const Topic = lazy(() => import('../pages/Library/Topic'));
const TopicResult = lazy(() => import('../pages/Library/Topic/Result'));
const TopicParsing = lazy(() => import('../pages/Library/Topic/Parsing'));
const ExamInfo = lazy(() => import('../pages/Library/Exam/Info'));
const Exam = lazy(() => import('../pages/Library/Exam'));
const ExamResult = lazy(() => import('../pages/Library/Exam/Result'));
const ExamParsing = lazy(() => import('../pages/Library/Exam/Parsing'));
const Record = lazy(() => import('../pages/UserCenter/Record'));
const PrivacyPolicy = lazy(
  () => import('../pages/Login/Agreement/PrivacyPolicy')
);
const UserService = lazy(() => import('../pages/Login/Agreement/UserService'));

const Routes = (): React.ReactElement => {
  const stateObj = JSON.parse(
    storage.getSession(StorageKey.PROJECTSTATE) || '{}'
  );
  const [state, dispatch] = useReducer(reducer, {
    ...defaultStore,
    ...stateObj,
  });
  return (
    <ErrorBoundary>
      <ProviderContext.Provider value={{ state, dispatch }}>
        <Suspense fallback={<LoadingPage />}>
          <Switch>
            <RouterProxy
              exact
              path="/login"
              component={Login}
              title="菓子题库登录"
            />
            <RouterProxy
              exact
              path="/register"
              component={Register}
              title="菓子题库注册"
            />
            <RouterProxy
              exact
              path="/complete"
              component={Complete}
              title="新用户登录"
            />
            <RouterProxy
              exact
              path="/complete-all"
              component={CompleteAll}
              title="新用户登录"
            />
            <RouterProxy
              exact
              path="/complete-show"
              component={CompleteShow}
              title="新用户登录"
            />
            <RouterProxy
              exact
              path="/library"
              component={Home}
              title="菓子题库"
            />
            <RouterProxy exact path="/find" component={Home} title="菓子发现" />
            <RouterProxy
              exact
              path="/user-center"
              component={Home}
              title="菓子与我"
            />
            <RouterProxy
              exact
              path="/record"
              component={Record}
              title="菓子与我"
            />
            <RouterProxy exact path="/msg" component={Msg} title="消息中心" />
            <RouterProxy
              exact
              path="/msg-detail"
              component={MsgDetail}
              title="消息中心"
            />
            <RouterProxy
              exact
              path="/setting"
              component={Setting}
              title="通用设定"
            />
            <RouterProxy
              exact
              path="/safe"
              component={Safe}
              title="账号与安全"
            />
            <RouterProxy
              exact
              path="/change-pwd"
              component={ChangePwd}
              title="更改密码"
            />
            <RouterProxy
              exact
              path="/feedback"
              component={Feedback}
              title="意见反馈"
            />
            <RouterProxy
              exact
              path="/quick"
              component={Quick}
              title="智能速答"
            />
            <RouterProxy
              exact
              path="/quick-result"
              component={QuickResult}
              title="智能速答"
            />
            <RouterProxy
              exact
              path="/quick-parsing"
              component={Parsing}
              title="智能速答"
            />
            <RouterProxy
              exact
              path="/topic"
              component={Topic}
              title="专题模拟"
            />
            <RouterProxy
              exact
              path="/topic-result"
              component={TopicResult}
              title="专题模拟"
            />
            <RouterProxy
              exact
              path="/topic-parsing"
              component={TopicParsing}
              title="专题模拟"
            />
            <RouterProxy exact path="/exam" component={Exam} title="套卷模拟" />
            <RouterProxy
              exact
              path="/exam-info"
              component={ExamInfo}
              title="套卷模拟"
            />
            <RouterProxy
              exact
              path="/exam-result"
              component={ExamResult}
              title="套卷模拟"
            />
            <RouterProxy
              exact
              path="/exam-parsing"
              component={ExamParsing}
              title="套卷模拟"
            />
            <RouterProxy
              exact
              path="/privacy-policy"
              component={PrivacyPolicy}
              title="隐私政策"
            />
            <RouterProxy
              exact
              path="/user-service"
              component={UserService}
              title="用户服务协议"
            />
            <Redirect to="/library" />
          </Switch>
        </Suspense>
      </ProviderContext.Provider>
    </ErrorBoundary>
  );
};

export default Routes;
